@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap");
:root {
  --font-Nunito-Sans: "Nunito Sans", sans-serif;
}

:root {
  --box-shadow: rgba(0, 0, 0, 0.393);
  /* ------------------------------------------------------------------------- */
  /*                                Light Mode;                                */
  /* ------------------------------------------------------------------------- */
  --light-theme-background: hsl(0, 0%, 98%);
  --light-theme-element-background: hsl(0, 0%, 100%);
  --light-theme-input: hsl(0, 0%, 52%);
  --light-theme-dark-blue-text: hsl(200, 15%, 8%);
  /* -------------------------------------------------------------------------- */
  /*                                  Dark Mode                                 */
  /* -------------------------------------------------------------------------- */
  --dark-theme-text-color: hsl(0, 0%, 100%);
  --dark-theme-dark-blue-background: hsl(207, 26%, 17%);
  --dark-theme-dark-blue-elements: hsl(208, 23%, 22%);
  /* -------------------------------------------------------------------------- */
  /*                        System Color Scheme Detector                        */
  /* -------------------------------------------------------------------------- */
}
@media (prefers-color-scheme: light) {
  :root {
    --background: var(--light-theme-background);
  }
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-theme-dark-blue-background);
    --text-color: var(--dark-theme-text-color);
    --background-elements: var(--dark-theme-dark-blue-elements);
  }
}

body.dark {
  --background: var(--dark-theme-dark-blue-background);
}

body.light {
  --background: var(--light-theme-background);
}

/*---------------------------
** CSS reset
Source credits :
-- See https://piccalil.li/blog/a-modern-css-reset/
-- 7-1 architecture - https://www.learnhowtoprogram.com/user-interfaces/building-layouts-preprocessors/7-1-sass-architecture
-----------------------------*/
/* Set core root defaults  */
html {
  box-sizing: border-box;
  font-size: 100%;
}
html:focus-within {
  scroll-behavior: smooth;
}
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    /* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
    scroll-behavior: auto;
  }
}

*,
*::before,
*::after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}
@media (prefers-reduced-motion: reduce) {
  *,
*::before,
*::after {
    /* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
  }
}

/* Remove default margin and preventing margin collapse
see -- https://developer.mozilla.org/en-US/docs/Web/Guide/CSS/Block_formatting_context/
*/
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  line-height: 1.5;
  text-rendering: optimizeSpeed;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  display: block;
  max-width: 100%;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

.common-background, .search__bar, .search__filter, .search__results, .header, .card__content {
  background-color: var(--background-elements);
  border-radius: 0.3125rem;
  padding: 1.25rem 1.5625rem;
}

.card {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1.25rem;
  color: var(--text-color);
  margin-top: 1.875rem;
}
.card__content {
  width: min(37.5rem, 100%);
}

body {
  background-color: var(--background);
  font-family: var(--font-Nunito-Sans);
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.c-wrapper {
  align-self: center;
  margin-top: 1.875rem;
  width: min(75rem, 100%);
  margin: 2.5rem 0.625rem;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.25rem 0.625rem;
  box-shadow: 2px 2px 2px var(--box-shadow);
}
@media (min-width: 40em) {
  .header {
    padding: 1.25rem;
  }
}
.header__title {
  color: var(--text-color);
  font-weight: 800;
  font-size: 0.9375rem;
}
@media (min-width: 40em) {
  .header__title {
    font-size: 1.25rem;
  }
}
.header__toggle {
  color: var(--text-color);
  display: block;
  font-weight: 400;
  font-size: 0.9375rem;
}
@media (min-width: 40em) {
  .header__toggle {
    font-size: 1.25rem;
  }
}
.header__toggle.fa-moon {
  margin-right: 0.125rem;
}
.search {
  color: var --text-color;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}
@media (min-width: 40em) {
  .search {
    flex-direction: row;
    margin: 0;
  }
}
.search__bar {
  width: min(31.25rem, 100%);
}
.search__input {
  background-color: transparent;
  outline: none;
  border: none;
  color: var(--text-color);
}
.search__inputplaceholder {
  color: var(--text-color);
}
.search__filter {
  color: var(--text-color);
  position: relative;
  width: 12.5rem;
  font-weight: 600;
  text-transform: capitalize;
  margin-top: 1.875rem;
}
@media (min-width: 40em) {
  .search__filter {
    margin-top: 0rem;
  }
}
.search__filter-dropdown {
  right: 1.5625rem;
  transform: translateY(50%);
  text-align: center;
  font-size: 0.8125rem;
  position: absolute;
}
.search__results {
  position: absolute;
  top: 4.375rem;
  width: 100%;
  left: 0;
  display: none;
}
.search__results-country {
  display: block;
  padding: 0.3125rem 0;
  letter-spacing: 1.5;
}